<template>
    <WindowWraper @hideWindow="$emit('hideWindow')" :isShow="isShow">
        <div class="auth__window" @click.stop>
            <CloseButton class="auth__window__close" @click="$emit('hideWindow')" />
            <LoginWindow @hideWindow="$emit('hideWindow')" v-if="login" @notHaveAccount="setSignup" @forgotPassword="setForgot" />
            <RegisterWindow @hideWindow="$emit('hideWindow')" v-if="signup" @haveAccount="setLogin" />
            <ForgotPasswordWindow v-if="forgot" @back="setLogin" />
        </div>
    </WindowWraper>
</template>

<style>
    .auth__window {
        padding: 40px 20px;
        background: var(--background);
        margin: auto;
        border-radius: 15px;
        border-top: 5px solid var(--primary-color);
        border-bottom: 5px solid var(--primary-color);
        position: relative;
    }

    .auth__window__close {
        position: absolute;
        right: 10px;
        top: 10px;
    }
</style>
  
<script>
import WindowWraper from './WindowWraper.vue'
import RegisterWindow from '../sections/RegisterWindow.vue'
import LoginWindow from '../sections/LoginWindow.vue'
import ForgotPasswordWindow from '../sections/ForgotPasswordWindow.vue'
import CloseButton from '../UI/CloseButton.vue'

export default {
    data() {
        return {
            login: true,
            signup: false,
            forgot: false
        }
    },
    emits: ["hideWindow"],
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
    },
    components: {
        WindowWraper,
        RegisterWindow,
        LoginWindow,
        CloseButton,
        ForgotPasswordWindow
    },
    methods: {
        setLogin() {
            this.login = true
            this.signup = false
            this.forgot = false
        },
        setSignup() {
            this.login = false
            this.signup = true
            this.forgot = false
        },
        setForgot() {
            this.login = false
            this.signup = false
            this.forgot = true
        }
    }
}
</script>