import axios from "axios"


const CityService = {
    async getCities({
        page = null,
        per_page = null,
        sort_by = null,
        search = null,
    } = {}) {
        const response = await axios.get("/api/cities/", { params: arguments[0] })
        return response.data
    },
    async getCurrentCity() {
        if (!localStorage.currentCity) {
            await this.setDefaultCurrentCity()
        }
        return JSON.parse(localStorage.currentCity)
    },
    async setDefaultCurrentCity() {
        const response = await axios.get("/api/cities/", { params: { "per_page": 1, "search": "москва" } })
        this.setCurrentCity(response.data.results[0])
    },
    setCurrentCity(currentCity) {
        localStorage.currentCity = JSON.stringify(currentCity)
    },
}

export default CityService