<template>
    <Teleport to="body">
        <div class="current-user-window__dialog" :style="{ top: y + 'px', left: x + 'px' }">
            <div class="current-user__dialog__content">
                <CloseButton class="current-user-window__close-button" @click="$emit('hideDialog')" />
                <div class="current-user__links">
                    <MenuLink class="current-user__link" to="/cabinet">Мой профиль</MenuLink>
                </div>
                <AuthButton class="current-user__button" @click="logoutUser">Выйти</AuthButton>
            </div>
        </div>
    </Teleport>
</template>

<style>
.current-user-window__dialog {
    z-index: 2;
    padding: 45px 15px 20px 15px;
    border-radius: 15px;
    box-shadow: 7px -4px 4px rgb(204, 204, 204);
    border: 2px solid var(--primary-color);
    background-color: var(--window-background);
    position: absolute;
}

.current-user-window__dialog__content {
    position: relative;
}

.current-user-window__close-button {
    margin-left: auto;
    position: absolute;
    right: 10px;
    top: 10px;
}

.current-user__links {
    padding-left: 5px;
    border-left: 2px solid var(--primary-color);
}

.current-user__link {
    display: block;
    font-size: 16px;
}

.current-user__button {
    display: block;
    margin-top: 15px;
    width: 172px;
    height: 30px;
    font-size: 16px;
}
</style>

<script>
import CloseButton from '../UI/CloseButton.vue'
import MenuLink from '../UI/MenuLink.vue'
import AuthButton from '../UI/AuthButton.vue'
import AuthService from '@/services/AuthService'

export default {
    components: {
    CloseButton,
    MenuLink,
    AuthButton,
},
    emits: ["hideDialog"],
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
        x: {
            type: Number,
            required: true
        },
        y: {
            type: Number,
            required: true
        }
    },
    methods: {
        async logoutUser() {
            await AuthService.logout()
            this.$store.dispatch("updateIsAuth")
            this.$emit('hideDialog')
        }
    },
}
</script>