<template>
    <div @click="isShowDialog ? hideDialog() : showDialog()" ref="currentUser" class="header__current-auth">
        <p class="header__current-auth__username"> {{ $store.state.currentUser?.username }} </p>
        <img class="header__current-auth__image" src="../../assets/noavatar.svg">
    </div>
    <CurrentUserWindow
        @hideDialog="isShowDialog=false"
        :x="modalX"
        :y="modalY"
        v-if="isShowDialog"
        :isShow="isShowDialog"
    />
</template>

<style>
.header__current-auth {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-left: 16px;
    cursor: pointer;
}

.header__current-auth__image {
    width: 35px;
    height: 35px;
    border-radius: 100%;

}

.header__current-auth__username {
    font-weight: 600;
    font-size: 16px;
}
</style>
  
<script>
import CurrentUserWindow from '../windows/CurrentUserWindow.vue'


export default {
    components: {
        CurrentUserWindow
    },
    data() {
        return {
            currentUser: null,
            modalX: 0,
            modalY: 0,
            isShowDialog: false
        }
    },
    async created() {
        this.$store.dispatch("updateCurrentUser")
    },
    methods: {
        hideDialog() {
            this.isShowDialog = false
        },
        showDialog() {
            this.setModalPositions()
            this.isShowDialog = true
        },
        setModalPositions() {
            this.modalX = this.$refs.currentUser.getBoundingClientRect().left - 80 + window.scrollX
            if (this.modalX > window.innerWidth - 210) {
                this.modalX = window.innerWidth - 210
            }
            this.modalY = this.$refs.currentUser.getBoundingClientRect().top + 40 + window.scrollY
        },
    }
}
</script>