import axios from "axios"
import AuthService from "./AuthService"


const PromoService = {
    async like(product_id) {
        let headers = await AuthService.getAuthHeader()
        const response = await axios.post(`/api/products/${product_id}/likes/`, null, { headers: headers })
        return response.data
    },
    async unlike(product_id) {
        let headers = await AuthService.getAuthHeader()
        const response = await axios.delete(`/api/products/${product_id}/likes/${AuthService.getCurrentUserId()}/`, { headers: headers })
        return response.data
    },
    async getLikes({ page = null, per_page = null } = {}) {
        let headers = await AuthService.getAuthHeader()
        const response = await axios.get(`/api/users/${AuthService.getCurrentUserId()}/likes/`, { headers: headers, params: arguments[0] })
        return response.data
    }
}

export default PromoService