import axios from "axios"
import { Buffer } from "buffer"
import store from "@/store"


const AuthService = {
    async register(username, email, password) {
        let data = new FormData()
        data.append("username", username)
        data.append("email", email)
        data.append("password", password)
        const response = await axios.post("/api/auth/signup/", data)
        localStorage.access = response.data.access
        localStorage.refresh = response.data.refresh
    },
    async login(email, password) {
        let data = new FormData()
        data.append("email", email)
        data.append("password", password)
        const response = await axios.post("/api/auth/login/", data)
        localStorage.access = response.data.access
        localStorage.refresh = response.data.refresh
    },
    async logout() {
        if (localStorage.refresh) {
            let data = new FormData()
            data.append("refresh", localStorage.refresh)
            try {
                await axios.post("/api/auth/logout/", data)
            }
            catch (e) {}
            delete localStorage.access
            delete localStorage.refresh
        }
    },
    async resetPassword(identifier) {
        let data = new FormData()
        data.append("identifier", identifier)
        await axios.post("/api/auth/reset-password/", data)
    },
    async changePassword(uuid, password) {
        let data = new FormData()
        data.append("uuid", uuid)
        data.append("password", password)
        await axios.post("/api/auth/change-password/", data)
    },
    async refresh() {
        let data = new FormData()
        data.append("refresh", localStorage.refresh)
        const response = await axios.post("/api/auth/refresh/", data)
        localStorage.access = response.data.access
        localStorage.refresh = response.data.refresh
        return response.data
    },
    async getAuthHeader() {
        if (localStorage.access) {
            if (this.isTokenExpired(localStorage.access)) {
                try {
                    await this.refresh()
                }
                catch (e) {
                    await store.dispatch("updateCurrentUser")
                    store.dispatch("updateIsAuth")
                    return
                }
            }
            return {
                "Authorization": "Bearer " + localStorage.access
            }
        }
    },
    async getCurrentUser() {
        if (localStorage.access) {
            let headers = await this.getAuthHeader()
            if (headers) {
                const response = await axios.get("/api/users/" + this.getCurrentUserId(), {
                    headers: headers
                })
                return response.data
            }
        }
    },
    isAuth() {
        if (localStorage.access) {
            if (!this.isTokenExpired(localStorage.access)) {
                return true
            }
        }
        return false
    },
    getCurrentUserId() {
        if (localStorage.access) {
            return this.getTokenInfo(localStorage.access).user_id
        }
    },
    isTokenExpired(token) {
        const info = this.getTokenInfo(token)
        if (Date.now() < info.exp * 1000) {
            return false
        }
        return true
    },
    getTokenInfo(token) {
        let rawInfo = token.split(".")[1]
        let info = JSON.parse(Buffer.from(rawInfo, "base64"))
        return info
    }
}


export default AuthService