<template>
    <Teleport to="body">
        <div class="city-choice__dialog" :style="{ top: y + 'px', left: x + 'px' }">
            <div class="city-chioce__dialog__content">
                <CloseButton class="city-choice__close-button" @click="$emit('hideDialog')" />
                <div class="city-choice__header">
                    <p class="city-choice__header__title">Выберите свой город</p>
                </div>
                <div ref="search" class="search city-choice__search">
                    <input v-model="searchQuery" class="search__input" type="text" autocomplete="off"
                        placeholder="Введите запрос">
                    <img src="../../assets/search.svg">
                </div>
                <div class="city-choice__cities">
                    <div v-for="city in cities" class="city-choice__city"
                        :class="{ active: $store.state.currentCity?.id == city.id }" @click="choiceCity(city)">
                        {{ city.name }}
                    </div>
                </div>
            </div>
        </div>
    </Teleport>
</template>

<style>
.city-choice__dialog {
    z-index: 21;
    padding: 20px 15px;
    border-radius: 15px;
    box-shadow: 7px -4px 4px rgb(204, 204, 204);
    border: 2px solid var(--primary-color);
    background-color: var(--window-background);
    position: absolute;
}

.city-choice__dialog__content {
    position: relative;
}

.city-choice__close-button {
    margin-left: auto;
    position: absolute;
    right: 10px;
    top: 10px;
}

.city-choice__header {
    display: flex;
    align-items: center;
}

.city-choice__header__title {
    font-size: 18px;
    font-weight: 800;
    margin: auto;
}

.city-choice__search {
    margin-top: 15px;
}

.city-choice__cities {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    border-left: 2px solid var(--primary-color);
    height: 95px;
}

.city-choice__city {
    padding: 5px;
    border-radius: 0 8px 8px 0;
    cursor: pointer;
}

.city-choice__city.active {
    font-weight: 700;
}

.city-choice__city:hover {
    background: var(--window-background__hover);
}
</style>

<script>
import CloseButton from '../UI/CloseButton.vue'
import CityService from '@/services/CityService'

export default {
    components: {
        CloseButton
    },
    emits: ["hideDialog", "newCurrentCity"],
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
        x: {
            type: Number,
            required: true
        },
        y: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            searchQuery: "",
            cities: [],
            searchDelay: null
        }
    },
    methods: {
        async updateCities() {
            let cities = await this.getCities()
            this.cities = cities
        },
        async getCities() {
            const response = await CityService.getCities({ page: 1, per_page: 3, search: this.searchQuery })
            return response.results
        },
        choiceCity(city) {
            this.$emit("newCurrentCity", city)
        }
    },
    async created() {
        await this.updateCities()
    },
    watch: {
        searchQuery(newValue) {
            if (this.searchQuery.length > 2) {
                if (this.searchDelay) {
                    clearTimeout(this.searchDelay);
                    this.searchDelay = null;
                }
                this.searchDelay = setTimeout(() => {
                    this.updateCities()
                }, 500);
            }
        }
    }
}
</script>