<template>
    <div class="home container">
        <div class="home__donors-section">
            <Title title="SaleHunter" description="Поиск скидок по всему интернету"></Title>
            <DonorCardSet :donors="donors" class="donors" />
        </div>
        <div class="home__discount-section">
            <Title title="Наибольшая выгода" description="Самые выгодные предложения"></Title>
            <ProductCardSet :products="products" />
        </div>
    </div>
</template>

<style>
.home__discount-section {
    margin-top: 75px;
}
</style>

<script>
import Title from '@/components/UI/Title.vue'
import DonorCardSet from '@/components/cardSets/DonorCardSet.vue'
import DonorService from '@/services/DonorService'
import ProductCardSet from '@/components/cardSets/ProductCardSet.vue'
import ProductService from '@/services/ProductService'


export default {
    data() {
        return {
            donors: [],
            products: []
        }
    },
    components: {
        Title,
        DonorCardSet,
        ProductCardSet
    },
    created() {
        this.pushDonors()
        this.pushProducts()
    },
    methods: {
        async pushDonors() {
            const donors = await DonorService.getDonors()
            this.donors = donors.slice(0, 5)
        },
        async pushProducts() {
            const products = await ProductService.getProducts({page: 1, per_page: 4, sort_by: "-discount", is_expired: false, city: this.currentCity})
            this.products = products.results
        }
    },
    computed: {
        currentCity() {
            return this.$store.state.currentCity?.id
        }
    },
    watch: {
        currentCity(oldValue, newValue) {
            this.pushProducts()
        }
    }
}
</script>
