import axios from "axios"
import AuthService from "./AuthService";


const ProductService = {
    async getProducts({
        page = null,
        per_page = null,
        sort_by = null,
        search = null,
        price__lt = null,
        price__gt = null,
        is_expired = null,
        promo_type = null,
        city = null
    } = {}) {
        let headers = await AuthService.getAuthHeader()
        const response = await axios.get("/api/products/", { params: arguments[0], headers: headers })
        return response.data
    },
    async getProduct(pk) {
        let headers = await AuthService.getAuthHeader()
        const response = await axios.get("/api/products/" + pk, { headers: headers })
        return response.data
    },
    async getProductShops(product_pk, { page = null, per_page = null, city = null } = {}) {
        const response = await axios.get("/api/products/" + product_pk + "/shops/", { params: arguments[1] })
        return response.data
    },
    async getSimiliar(product_pk, { page = null, per_page = null, city = null, is_expired = null } = {}) {
        const response = await axios.get("/api/products/" + product_pk + "/similiar/", { params: arguments[1] })
        return response.data
    }
}

export default ProductService