<template>
    <div class="like-icon">
        <img @click="unlike" class="like-icon__image" v-if="isLiked" src="../../assets/like.svg">
        <img @click="like" class="like-icon__image" v-else src="../../assets/unlike.svg">
        <ErrorWindow :isShow="isShowError">Авторизуйтесь, чтобы добавлять акции в избранное</ErrorWindow>
    </div>
</template>

<style>
.like-icon {
    z-index: 3;
}
.like-icon__image {
    width: 100%;
    height: 100%;
}
</style>

<script>
import LikeService from '@/services/LikeService'
import ErrorWindow from '../windows/ErrorWindow.vue'


export default {
    data() {
        return {
            isLiked: null,
            isShowError: false
        }
    },
    props: {
        product: {
            type: Object,
            reqired: true
        }
    },
    components: {
        ErrorWindow
    },
    created() {
        this.isLiked = this.product.is_liked
    },
    methods: {
        async like() {
            if (this.$store.state.isAuth) {
                await LikeService.like(this.product.id)
                this.isLiked = true
            }
            else {
                this.isShowError = true
                setTimeout(() => {
                    this.isShowError = false
                }, 2000)
            }
        },
        async unlike() {
            await LikeService.unlike(this.product.id)
            this.isLiked = false
        }
    }
}
</script>