<template>
    <div class="product-card" :class="{ expired: isExpired }">
        <ProductImage class="product-card__poster" :src="product.poster" :procentDiscount="procentDiscount" />
        <div class="product-card__info">
            <keep-alive>
                <router-link :to="`/products/${product.id}`" class="product-card__info__title">{{ product.title }}</router-link>
            </keep-alive>
            <div class="product-card__info__like">
                <LikeIcon class="product-card__info__like__image" :product="product"/>
            </div>
            <div class="product-card__info__detail">
                <div class="product-card__info__detail__section">
                    <ul class="product-card__info__attrs">
                        <li>{{ product.donor.name }}</li>
                        <li><span class="product-card__info__attrs__key">Акция:</span> {{ product.promo.type.name }}</li>
                    </ul>
                </div>
                <div class="product-card__info__detail__section product-card__info__detail__section__right">
                    <div class="product-card__info__price">
                        <p class="product-card__info__price__normal-price">{{ product.price }}р.</p>
                        <p class="product-card__info__price__promo-price">
                            <span class="primary-color">{{ product.promo_price }}</span>р.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style>
