    align-self: flex-end;
}

.product-card__info__price__normal-price {
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 1.1px;
    text-decoration-line: line-through;
    color: var(--additional-text);
    text-align: right;
}

.product-card__info__price__promo-price {
    font-weight: 700;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 1.1px;
    text-align: right;
}

.product-card__info__attrs {
    list-style: none;
}

.product-card__info__attrs>li {
    font-weight: 400;
