</style>

<script>
import ProductImage from '../UI/ProductImage.vue'
import LikeIcon from '../UI/LikeIcon.vue';


export default {
    components: {
        ProductImage,
        LikeIcon
    },
    props: {
        product: {
            type: Object,
            required: true
        }
    },
    computed: {
        procentDiscount() {
            return 100 - Math.round(this.product.promo_price * 100 / this.product.price)
        },
        isExpired(){
            let date_end = new Date(this.product.promo.date_end)
            date_end.setDate(date_end.getDate() + 1);
            let now = new Date()
            return now > date_end
        }
    },
}
</script>