import { createStore } from 'vuex'
import CityService from '@/services/CityService'
import AuthService from '@/services/AuthService'


export default createStore({
    state: {
        currentCity: null,
        isAuth: null,
        currentUser: null
    },
    getters: {
    },
    mutations: {
        setCurrentCity(action, payload) {
            action.currentCity = payload
        },
        setIsAuth(action, payload) {
            action.isAuth = payload
        },
        setCurrentUser(action, payload) {
            action.currentUser = payload
        }
    },
    actions: {
        updateCurrentCity: async ({ commit }) => {
            commit("setCurrentCity", await CityService.getCurrentCity())
        },
        setCurrentCity: (context, payload) => {
            CityService.setCurrentCity(payload)
            context.commit("setCurrentCity", payload)
        },
        updateIsAuth({ commit }) {
            commit("setIsAuth", AuthService.isAuth())
        },
        async updateCurrentUser({ commit }) {
            let user = await AuthService.getCurrentUser()
            commit("setCurrentUser", user)
        }
    },
})
