<template>
    <h1 class="auth__window__title">Регистрация</h1>
    <form @submit="registerUser" class="auth__window__form" action="/api/register" method="POST">
        <p v-if="errors.username" class="auth__window__error-message">{{ errors.username.join("\n") }}</p>
        <AuthInput v-model:modelValue="username" placeholder="Имя пользователя" name="username" class="auth__window__form__input" type="text" />
        <p v-if="errors.email" class="auth__window__error-message">{{ errors.email.join("\n") }}</p>
        <AuthInput v-model="email" placeholder="Почта" name="email" class="auth__window__form__input" type="email" />
        <p v-if="errors.password" class="auth__window__error-message">{{ errors.password.join("\n") }}</p>
        <AuthInput v-model="password" placeholder="Пароль" name="password" class="auth__window__form__input" type="password" />
        <AuthInput v-model="confirmPassword" placeholder="Повторите пароль" name="password" class="auth__window__form__input" type="password" />
        <AuthButton type="submit" class="auth__window__form__button">Зарегистрироваться</AuthButton>
    </form>
    <p @click="$emit('haveAccount')" class="auth__window__form__change-component">Уже есть аккаунт?</p>
</template>

<style scoped>
.auth__window__title {
    color: var(--primary-color);
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    margin-bottom: 30px;
}

.auth__window__error-message {
    margin: auto;
    background: var(--primary-color);
    margin-bottom: 10px;
    width: 250px;
    padding: 10px 0;
    color: var(--background);
    text-align: center;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    white-space: pre-line;
}

.auth__window__form__input {
    margin-bottom: 20px;
}

.auth__window__form__button {
    width: 172px;
    height: 40px;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    display: block;
    margin: auto;
}

.auth__window__form__change-component {
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: var(--primary-color);
    cursor: pointer;
    margin-top: 10px;
}
</style>
  
<script>
import AuthButton from '../UI/AuthButton.vue'
import AuthInput from '../UI/AuthInput.vue'
import AuthService from '@/services/AuthService'


export default {
    emits: ["hideWindow", "haveAccount"],
    data() {
        return {
            username: "",
            email: "",
            password: "",
            confirmPassword: "",
            errors: {}
        }
    },
    components: {
        AuthButton,
        AuthInput
    },
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
    },
    methods: {
        async registerUser(event) {
            event.preventDefault()
            if (this.password != this.confirmPassword) {
                this.errors = {password: ["Пароли не совпадают"]}
                return
            }
            try {
                await AuthService.register(this.username, this.email, this.password)
                this.$store.dispatch("updateIsAuth")
                this.$store.dispatch("updateCurrentUser")
                this.$emit("hideWindow")
            }
            catch (e) {
                if (e.response.status === 400) {
                    this.errors = e.response.data
                }
            }
        }
    }
}
</script>