<template>
    <Header @showAuthWindow="showAuthWindow" @hideAuthWindow="hideAuthWindow" :isShowAuthWindow="isShowAuthWindow"></Header>
    <main>
        <router-view @showAuthWindow="showAuthWindow" @hideAuthWindow="hideAuthWindow" />
    </main>
</template>

<style>
:root {
    --background: #FFFFFF;
    --window-background: #f8f6f6;
    --window-background__hover: #e6e4e4;
    --default-text: #323232;
    --default-text__hover: #000000;
    --primary-color: #C69AFF;
    --primary-color__hover: #b177fd;
    --additional-text: #707070;
}

body {
    background-color: var(--background);
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    color: var(--default-text);
}

.container {
    width: 90%;
    max-width: 1300px;
    margin: auto;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.primary-color {
    color: var(--primary-color);
}

.no-scroll {
    overflow: hidden;
}

main {
    padding-bottom: 30px;
    margin-top: 75px;
}
</style>

<script>
import Header from './components/Header.vue'
import AuthService from './services/AuthService'


export default {
    data() {
        return {
            isShowAuthWindow: false
        }
    },
    components: {
        Header
    },
    async beforeCreate() {
        await this.$store.dispatch("updateCurrentCity");
    },
    async created() {
        if (localStorage.refresh) {
            try {
                await AuthService.refresh()
            }
            catch (e) {

            }
        }
        this.$store.dispatch("updateIsAuth")
        this.$store.dispatch("updateCurrentUser")
    },
    methods: {
        showAuthWindow() {
            this.isShowAuthWindow = true
        },
        hideAuthWindow() {
            this.isShowAuthWindow = false
        }
    }
}
</script>