<template>
    <a ref="cityChoice" class="menu__city-choice menu__link" @click="isShowDialog ? hideDialog() : showDialog()">
        <img class="menu__city-choice__image" src="../../assets/geo.svg">
        {{ $store.state.currentCity?.name }}
    </a>
    <CityChoiceWindow @hideDialog="hideDialog" @newCurrentCity="setNewCurrentCity" :x="modalX" :y="modalY"
        v-if="isShowDialog" :isShow="isShowDialog" />
</template>

<style>
.menu__city-choice {
    display: flex;
    align-items: center;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;
}

.menu__city-choice__image {
    width: 20px;
    height: 20px;
}
</style>

<script>
import CityChoiceWindow from '../windows/CityChoiceWindow.vue'


export default {
    components: {
        CityChoiceWindow
    },
    emits: ["hideDialog", "showDialog"],
    props: {
        isShowDialog: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            modalX: 0,
            modalY: 0
        }
    },
    methods: {
        hideDialog() {
            this.$emit("hideDialog")
        },
        showDialog() {
            this.setModalPositions()
            this.$emit("showDialog")
        },
        setModalPositions() {
            this.modalX = this.$refs.cityChoice.getBoundingClientRect().left - 100 + window.scrollX
            if (this.modalX < 0) {
                this.modalX = 10
            }
            this.modalY = this.$refs.cityChoice.getBoundingClientRect().top + 30 + window.scrollY
        },
        setNewCurrentCity(newCurrentCity) {
            this.$store.dispatch("setCurrentCity", newCurrentCity)
        }
    },
}
</script>