<template>
    <h1 class="auth__window__title">Вход</h1>
    <form @submit="loginUser" class="auth__window__form" method="POST">
        <p v-if="errors.detail" class="auth__window__error-message">{{ errors.detail }}</p>
        <p v-if="errors.email" class="auth__window__error-message">{{ errors.email.join("\n") }}</p>
        <AuthInput v-model="email" placeholder="Почта" name="email" class="auth__window__form__input" type="email" />
        <p v-if="errors.password" class="auth__window__error-message">{{ errors.password.join("\n") }}</p>
        <AuthInput v-model="password" placeholder="Пароль" name="password" class="auth__window__form__input" type="password" />
        <p @click="$emit('forgotPassword')" class="auth__window__form__change-component forgot-password">Не помню пароль</p>
        <AuthButton type="submit" class="auth__window__form__button">Войти</AuthButton>
    </form>
    <p @click="$emit('notHaveAccount')" class="auth__window__form__change-component">Нет аккаунта?</p>
</template>

<style scoped>
.auth__window__title {
    color: var(--primary-color);
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    margin-bottom: 10px;
}

.auth__window__form__input {
    margin-top: 20px;
}

.auth__window__error-message {
    margin: auto;
    background: var(--primary-color);
    margin-top: 20px;
    margin-bottom: -10px;
    width: 250px;
    padding: 10px 0;
    color: var(--background);
    text-align: center;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    white-space: pre-line;
}

.auth__window__form__button {
    width: 172px;
    height: 40px;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    display: block;
    margin: auto;
}

.auth__window__form__change-component {
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: var(--primary-color);
    cursor: pointer;
    margin-top: 10px;
}

.auth__window__form__change-component.forgot-password {
    margin-bottom: 20px;
    margin-top: 5px;
}
</style>
  
<script>
import AuthButton from '../UI/AuthButton.vue'
import AuthInput from '../UI/AuthInput.vue'
import AuthService from '@/services/AuthService'


export default {
    emits: ["hideWindow", "notHaveAccount", "forgotPassword"],
    components: {
        AuthButton,
        AuthInput
    },
    data() {
        return {
            email: "",
            password: "",
            errors: {}
        }
    },
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
    },
    methods: {
        async loginUser(event) {
            event.preventDefault()
            try {
                await AuthService.login(this.email, this.password)
                this.$store.dispatch("updateIsAuth")
                this.$store.dispatch("updateCurrentUser")
                this.$emit("hideWindow")
            }
            catch (e) {
                if (String(e.response.status)[0] === "4") {
                    this.errors = e.response.data
                }
            }
        }
    }
}
</script>
