<template>
    <h1 class="auth__window__title">Восстановление пароля</h1>
    <p class="auth__window__success" v-if="isSuccess">
        В скором времени к вам на почту придёт письмо с ссылкой для восстановления пароля, следуйте инструкциям.
    </p>
    <form v-else @submit="resetPassword" class="auth__window__form" method="POST">
        <p v-if="errors.detail" class="auth__window__error-message">{{ errors.detail }}</p>
        <p v-if="errors.identifier" class="auth__window__error-message">{{ errors.identifier.join("\n") }}</p>
        <AuthInput v-model="identifier" placeholder="Имя пользователя или почта" name="identifier" class="auth__window__form__input" type="text" />
        <AuthButton type="submit" class="auth__window__form__button">Продолжить</AuthButton>
    </form>
    <p @click="$emit('back')" class="auth__window__form__change-component">На страницу входа</p>
</template>

<style scoped>
.auth__window__title {
    color: var(--primary-color);
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    margin: auto;
    margin-bottom: 10px;
    max-width: 300px;
}

.auth__window__form__input {
    margin-top: 20px;
}

.auth__window__error-message {
    margin: auto;
    background: var(--primary-color);
    margin-top: 20px;
    margin-bottom: -10px;
    width: 250px;
    padding: 10px 0;
    color: var(--background);
    text-align: center;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    white-space: pre-line;
}

.auth__window__form__button {
    width: 172px;
    height: 40px;
    border-radius: 15px;
    font-weight: 600;
    font-size: 14px;
    display: block;
    margin: auto;
    margin-top: 30px;
}

.auth__window__form__change-component {
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: var(--primary-color);
    cursor: pointer;
    margin-top: 10px;
}

.auth__window__form__change-component.forgot-password {
    margin-bottom: 20px;
    margin-top: 5px;
}

.auth__window__success {
    margin-top: 20px;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    width: 350px;
}
</style>
  
<script>
import AuthButton from '../UI/AuthButton.vue'
import AuthInput from '../UI/AuthInput.vue'
import AuthService from '@/services/AuthService'


export default {
    emits: ["back"],
    components: {
        AuthButton,
        AuthInput
    },
    data() {
        return {
            identifier: "",
            errors: {},
            isSuccess: false
        }
    },
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
    },
    methods: {
        async resetPassword(event) {
            event.preventDefault()
            try {
                await AuthService.resetPassword(this.identifier)
                this.isSuccess = true
            }
            catch (e) {
                if (String(e.response.status)[0] === "4") {
                    this.errors = e.response.data
                }
            }
        }
    }
}
</script>
