<template>
    <div ref="search" class="search">
        <input @focusin="showDialog" @focusout="hideDialog" @input="updateSearch" v-model="query" class="search__input"
            :placeholder="placeholder" type="text" autocomplete="off" name="search">
        <router-link :to="{ path: 'products', query: { search: query }}" class="search__button">
            <img src="../../assets/search.svg">
        </router-link>
        <SearchWindow :isShow="isShowDialog" :x="modalX" :y="modalY" :products="products" />
    </div>
</template>

<style>
.search {
    display: flex;
    align-items: center;
    border: 2px solid var(--primary-color);
    border-radius: 15px;
    width: 300px;
    padding-right: 8px;
}

.search__input {
    width: 100%;
    height: 40px;
    border: none;
    background: none;
    padding: 0 8px 0 18px;
    font-weight: 600;
    font-size: 15px;
    color: var(--additional-text);
}

.search__input:focus {
    outline: none;
    color: var(--default-text);
}

.search__button {
    border: none;
    background: none;
    cursor: pointer;
}

@media (max-width: 400px) {
    .search__input {
        font-size: 13px;
    }
}
</style>

<script>
import SearchWindow from '../windows/SearchWindow.vue'
import ProductService from '@/services/ProductService'

export default {
    data() {
        return {
            query: "",
            modalX: 0,
            modalY: 0,
            isShowDialog: false,
            products: [],
            searchDelay: null,
        }
    },
    components: {
        SearchWindow
    },
    props: {
        placeholder: {
            type: String,
            default: "Введите запрос"
        },
    },
    methods: {
        hideDialog() {
            this.isShowDialog = false
        },
        showDialog() {
            this.setModalPositions()
            this.isShowDialog = true
        },
        setModalPositions() {
            this.modalX = this.$refs.search.getBoundingClientRect().left + window.scrollX
            if (this.modalX > window.innerWidth - 210) {
                this.modalX = window.innerWidth - 210
            }
            this.modalY = this.$refs.search.getBoundingClientRect().top + 50 + window.scrollY
        },
        async updateSearch(event) {
            if (this.query.length >= 3) {
                if (this.searchDelay) {
                    clearTimeout(this.searchDelay);
                    this.searchDelay = null;
                }
                this.searchDelay = setTimeout(async () => {
                    const response = await ProductService.getProducts({ page: 1, per_page: 3, is_expired: false, search: this.query, city: this.currentCity })
                    this.products = response.results
                }, 500);
            }
            else {
                this.products = []
            }
        }
    },
    computed: {
        currentCity() {
            return this.$store.state.currentCity?.id
        },
    }
}
</script>